.source-page {
  min-height: 860px;
  max-width: 1240px;
  margin: auto;
  display: grid;
  grid-template-columns: 20% auto;
  grid-template-rows: 38px auto;
}

.source-page-sidebar {
  grid-row-start: 2;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-top: 40px;
  font-family: 'Roboto', sans-serif;
}

.source-page-identifier {
  -ms-flex-order: 1;
      order: 1;
  text-align: center;
}

.source-topics-container {
  -ms-flex-order: 2;
      order: 2;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.source-topics {
  margin-top: 40px;
  -webkit-animation-delay: .5s;
          animation-delay: .5s;
}

.source-page-main {
  grid-column: 2;
  grid-row-start: 2;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
}

.source-page-search-bar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  width: 50%;
  margin: auto;
  margin-top: 25px;
  border: solid;
  border-width: 1px;
  border-color: hsla(0, 0%, 0%, 0.1);
  border-radius: 20px;
}

.source-page-search-bar-icon {
  -ms-flex-order: 1;
      order: 1;
  margin-left: 10px;
  margin-top: 3px;
  color: #4f5b66;
}

.source-page-search-bar-icon:hover {
  cursor: pointer;
}

.source-page-search-input {
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-order: 2;
      order: 2;
  height: 30px;
  width: 50%;
  border: none;
  border-radius: 20px;
  font-size: 10pt;
  margin-left: 10px;
  margin-right: 10px;
}

.source-page-search-input:focus {
  outline: none;
}

/* Mobile Devices */
@media only screen and (max-width: 414px) {
  .source-page {
    max-width: 1240px;
    min-height: 736px;
    margin: auto;
    display: grid;
    grid-template-columns: 0;
    grid-template-rows: 38px auto auto;
  }

   .source-page-sidebar {
    grid-row-start: 2;
    width: 100vw;
    margin-top: 25px;
  }

  .source-topics {
    margin-top: 0;
  }

  .topics-header {
    visibility: hidden;
  }

  .topics-list {
    margin-top: 0;
    padding-left: 0;
    text-align: center;
  }

  .topics-list li {
    display: inline-block;
    margin-left: 8px;
    margin-right: 8px;
  }

  .source-page-main {
    grid-row-start: 3;
  }
}

/* Landscape Mobile Devices */
@media only screen and (max-width: 812px) and (orientation: landscape) {
  .source-page {
    max-width: 1240px;
    min-height: 736px;
    margin: auto;
    display: grid;
    grid-template-columns: 0;
    grid-template-rows: 38px 140px auto;
  }

   .source-page-sidebar {
    grid-row-start: 2;
    width: 100vw;
    margin-top: 25px;
  }

  .source-topics {
    margin-top: 0;
  }

  .topics-header {
    visibility: hidden;
  }

  .topics-list {
    margin-top: 0;
    padding-left: 0;
  }

  .topics-list li {
    display: inline-block;
    margin-left: 8px;
    margin-right: 8px;
  }

  .source-page-main {
    grid-row-start: 3;
  }
}



