.news-feed-container {
  width: 100%;
  margin-top: 40px;
}

.news-feed-card-link {
  text-decoration: none;
}

.news-feed-card {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 280px;
  height: 0%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 38px;
	border-radius: 8px;
  -webkit-box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
          box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  overflow: hidden;
  -webkit-transition: all 0.3s cubic-bezier(.25,.8,.25,1);
  -o-transition: all 0.3s cubic-bezier(.25,.8,.25,1);
  transition: all 0.3s cubic-bezier(.25,.8,.25,1);
  -webkit-animation-delay: .4s;
          animation-delay: .4s;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
}

.news-feed-card-img-container {
  -ms-flex-order: 1;
      order: 1;
  height: 220px;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  overflow: hidden;
  -webkit-animation-delay: .8s;
          animation-delay: .8s;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
}

.news-feed-card-img {
  max-width: 400px;
}

.news-feed-card-img-container:after {
  content: "";
  position: absolute;
  top: 190px;
  left: 0;
  border-style: solid;
  border-width: 0 0 30px 280px;
  border-color: transparent transparent white;
}

.news-feed-card-title {
  -ms-flex-order: 2;
      order: 2;
  margin-top: 10px;
  margin-bottom: 10px;
  padding-left: 15px;
  padding-right: 15px;
  word-wrap: break-word;
  overflow: hidden;
  font-weight: bold;
  color: #000000;
}

.news-feed-card-author {
  -ms-flex-order: 3;
      order: 3;
  margin-bottom: 10px;
  padding-left: 15px;
  padding-right: 15px;
  overflow: hidden;
  font-size: 12px;
  color: #999999;
}

.news-feed-card-description {
  -ms-flex-order: 4;
      order: 4;
  margin-bottom: 10px;
  padding-left: 15px;
  padding-right: 15px;
  overflow: hidden;
  font-family: Helvetica;
  font-size: 12px;
  color: #797979;
  text-align: left;
  line-height: 1.5;
}

/* Mobile Devices */
@media only screen and (max-width: 414px) {
  .news-feed-container {
    margin-top: 0;
  }

  .news-feed-card {
    margin-left: auto;
    margin-right: auto;
  }
}


